.page-header__toggle {
  position: relative;
  display: none;
  width: 50px;
  height: 24px;
  background-color: transparent;
  border: none;
}
.page-header__toggle:hover {
  cursor: pointer;
}
.page-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.page-header__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.page-header__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}
@media screen and (min-width: 1200px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__toggle--js-enabled {
  display: block;
}
@media screen and (min-width: 1200px) {
  .page-header__toggle--js-enabled {
    display: none;
  }
}
.page-header__toggle--close {
  width: 23px;
  height: 23px;
}
.page-header__toggle--close::before,
.page-header__toggle--close::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -2px;
  width: 27px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 0;
}
.page-header__toggle--close::after {
  transform: rotate(-45deg);
}
.page-header__toggle--close::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header__toggle--close:hover::after {
  background-color: #d22856;
  box-shadow: none;
}
.page-header__toggle--close:hover::before {
  background-color: #d22856;
  box-shadow: none;
}
.page-header__toggle--close:active::after {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: none;
}
.page-header__toggle--close:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: none;
}
