}
.button--green {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.6;
  color: #ffffff;
  background-color: #2aab6d;
  border: 2px solid #2aab6d;
  border-radius: 1px;
}
.button--green:hover {
  background-color: #1c9b5e;
  border-color: #1c9b5e;
}
.button--green:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #1c9b5e;
  border-color: #1c9b5e;
}
.button--pink {
  border: 3px solid #d22856;
  color: #d22856;
}
.button--pink:hover {
  background-color: #d22856;
  color: #ffffff;
}
.button--pink:active {
  background-color: #d22856;
  color: rgba(255, 255, 255, 0.3);
}
.button--gray {
  border: 3px solid rgba(40, 54, 69, 0.1);
  color: rgba(40, 54, 69, 0.1);
}
.button--gray:hover {
  color: rgba(40, 54, 69, 0.3);
  border-color: rgba(40, 54, 69, 0.3);
}
.button--gray:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(40, 54, 69, 0.3);
  border-color: transparent;
}
