  width: 280px;
}
@media screen and (min-width: 660px) {
  .gallery__img {
    width: 300px;
    height: 300px;
  }
}
@media screen and (min-width: 1200px) {
  .gallery__img {
    height: 400px;
  }
}
.gallery__img--top {
  width: 100%;
}
@media screen and (min-width: 660px) {
  .gallery__img--top {
    height: 509px;
  }
}
@media screen and (min-width: 1200px) {
  .gallery__img--top {
    height: 506px;
  }
}
.gallery__attn {
  position: absolute;
  top: 0;
  right: 20px;
  width: 140px;
  padding-top: 11px;
  padding-bottom: 11px;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #d22856;
}
@media screen and (min-width: 660px) {
  .gallery__attn {
    top: 57px;
  }
}
@media screen and (min-width: 1200px) {
  .gallery__attn {
    top: 50px;
    right: 130px;
  }
}
.gallery__text-block {
  width: 100%;
  max-width: 280px;
  margin: 0 auto;
  padding: 18px 10px 26px 17px;
  background-color: #f2f2f2;
}
@media screen and (min-width: 660px) {
  .gallery__text-block {
    display: flex;
    flex-direction: column;
    max-width: 300px;
    min-height: 148px;
    padding: 20px 30px;
  }
}
@media screen and (min-width: 1200px) {
  .gallery__text-block {
    min-height: 150px;
    padding-top: 23px;
  }
}
.gallery__text-block--top {
  width: 87.5%;
  max-width: none;
}
@media screen and (min-width: 660px) {
  .gallery__text-block--top {
    width: 94%;
    min-height: 120px;
    padding: 29px 30px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
}
@media screen and (min-width: 1200px) {
  .gallery__text-block--top {
    width: 78.5%;
    min-height: 92px;
    padding: 20px 30px;
  }
}
.gallery__autor-time {
  margin-bottom: 5px;
}
@media screen and (min-width: 660px) {
  .gallery__autor-time--top {
    margin-bottom: 0;
  }
}
.gallery__autor {
  font-size: 14px;
  font-weight: 700;
  color: #283645;
}
.gallery__time {
  font-size: 12px;
  font-weight: 300;
  color: #283645;
  text-transform: uppercase;
}
.gallery__caption {
  font-size: 14px;
  line-height: 1.7;
  font-weight: 400;
  color: #283645;
  margin-bottom: 15px;
}
@media screen and (min-width: 660px) {
  .gallery__caption {
    flex-grow: 1;
  }
}
@media screen and (min-width: 660px) {
  .gallery__caption--top {
    order: 3;
    width: 100%;
    margin-bottom: 0;
  }
  .gallery__caption--top br {
    display: none;
  }
}
@media screen and (min-width: 1200px) {
  .gallery__caption--top br {
    display: none;
  }
}
@media screen and (min-width: 660px) {
  .gallery__likes--top {
    width: 120px;
  }
}
.likes {
  display: flex;
  align-items: center;
}
@media screen and (min-width: 660px) {
  .likes {
    justify-content: space-between;
  }
}
.likes__heart {
  border: none;
  background-color: inherit;
  width: 13px;
  height: 12px;
  margin-right: 18px;
}
@media screen and (min-width: 660px) {
  .likes__heart {
    order: 2;
    margin-right: 0;
  }
}
.likes__number {
  font-size: 12px;
  font-weight: 400;
  color: #d22856;
  text-transform: uppercase;
}
@media screen and (min-width: 660px) {
  .likes__number {
    order: 1;
  }
}
.new-post {
  background-color: #f2f2f2;
}
@media screen and (min-width: 1200px) {
  .new-post {
    width: 78.5%;
    max-width: 1150px;
    margin: 0 auto 28px auto;
  }
}
.new-post__wrapper {
  padding: 20px 20px 40px 20px;
}
@media screen and (min-width: 660px) {
  .new-post__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 43px;
    padding: 55px 20px 45px;
  }
}
@media screen and (min-width: 1200px) {
  .new-post__wrapper {
    grid-template-columns: 300px 1fr;
    column-gap: 90px;
    padding: 51px 60px 57px 80px;
    background-color: #f2f2f2;
  }
}
.new-post__img-block {
  width: 100%;
  max-width: 280px;
  margin: 0 auto 40px auto;
}
@media screen and (min-width: 660px) {
  .new-post__img-block {
    grid-row: 1 / 3;
    width: 300px;
    max-width: none;
    height: 400px;
    margin-bottom: 0;
  }
}
.new-post__img {
  display: block;
}
.new-post__modification {
  margin-bottom: 23px;
}
@media screen and (min-width: 660px) {
  .new-post__modification {
    display: none;
  }
}
.new-post__range-block {
  padding-bottom: 30px;
  border-bottom: 1px solid rgba(40, 54, 69, 0.1);
}
@media screen and (min-width: 660px) {
  .new-post__range-block {
    padding-top: 10px;
    padding-bottom: 0;
    border-bottom: none;
  }
}
@media screen and (min-width: 1200px) {
  .new-post__range-block {
    padding-top: 0;
  }
}
.new-post__buttons {
  padding-top: 37px;
}
@media screen and (min-width: 660px) {
  .new-post__buttons {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-top: 0;
  }
}
@media screen and (min-width: 1200px) {
  .new-post__buttons {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.new-post__button {
  margin-bottom: 15px;
}
.new-post__button:last-child {
  margin-bottom: 0;
}
@media screen and (min-width: 660px) {
  .new-post__button {
    width: 93%;
    margin-bottom: 27px;
  }
}
@media screen and (min-width: 1200px) {
  .new-post__button {
    width: 46%;
    margin: 0;
  }
}
.modification__buttons {
  width: 208px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}
.modification__button {
  margin-right: 35px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.modification__button:last-child {
  margin-right: 0;
}
.modification__button--inactive svg {
  opacity: 0.3;
}
.modification__button--inactive svg:hover {
  opacity: 0.5;
}
.range-block__list {
  text-align: center;
}
.range-block__item {
  display: none;
}
@media screen and (min-width: 660px) {
  .range-block__item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 0 auto 22px auto;
    padding: 0 18px 22px 4px;
    border-bottom: 1px solid rgba(40, 54, 69, 0.1);
  }
  .range-block__item:last-child {
    margin-bottom: 0;
  }
}
@media screen and (min-width: 1200px) {
  .range-block__item {
    width: 100%;
    margin-bottom: 15px;
    padding: 0 30px 8px 0;
  }
  .range-block__item:last-child {
    border-bottom: none;
  }
}
.range-block__item--current {
  display: block;
}
@media screen and (min-width: 660px) {
  .range-block__item--current {
    display: flex;
  }
}
.range-block__img-wrap {
  display: none;
}
@media screen and (min-width: 660px) {
  .range-block__img-wrap {
    display: block;
    width: 24px;
  }
}
@media screen and (min-width: 1200px) {
  .range-block__img-wrap {
    width: 48px;
  }
}
@media screen and (min-width: 1200px) {
  .range-block__icon--visible-tablet {
    display: none;
  }
}
@media screen and (min-width: 660px) {
  .range-block__icon--visible-desktop {
    display: none;
  }
}
@media screen and (min-width: 1200px) {
  .range-block__icon--visible-desktop {
    display: inline-block;
  }
}
.range-block__runner {
  width: 240px;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.range-block__runner::-webkit-slider-runnable-track {
  height: 2px;
  background-color: #283645;
}
.range-block__runner::-moz-range-track {
  height: 2px;
  background-color: #283645;
}
.range-block__runner::-webkit-slider-thumb {
  background: #ffffff;
  border: 2px solid #d22856;
  border-radius: 100%;
  cursor: pointer;
  width: 14px;
  height: 14px;
  appearance: none;
  -webkit-appearance: none;
  margin-top: -6px;
}
.range-block__runner::-webkit-slider-thumb:active {
  background: #d22856;
}
.range-block__runner::-moz-range-thumb {
  background: #ffffff;
  border: 2px solid #d22856;
  border-radius: 100%;
  cursor: pointer;
  width: 14px;
  height: 14px;
  appearance: none;
  -webkit-appearance: none;
  margin-top: -6px;
}
.range-block__runner::-moz-range-thumb:active {
  background: #d22856;
}
@media screen and (min-width: 660px) {
  .range-block__runner {
    width: 81%;
  }
}
@media screen and (min-width: 1200px) {
  .range-block__runner {
    width: 76%;
  }
}
